.sponsor {
    text-align: center;
    padding-top: 50px;

    .sponsor-avatar {
        img {
            display: inline-block;
            width: 6rem;
            height: 6rem;
            margin: 10px;
            @include border-radius(100%);
            @include box-shadow(0 0 0 .3618em rgba(0, 0, 0, .05));
        }
    }

    .sponsor-custom {
        margin: 30px auto;
        display: block;
    }

    .sponsor-bio {
        width: 25%;
        margin: 10px auto;
    }

    .post-sponsor {

        #sponsor:checked~.qr-code {
            display: block;
        }

        #sponsor:checked~.sponsor-button {
            display: none;
        }
        .qr-code {
            display: none;

            .qr-code-image {
                display: inline-block;
                min-width: 200px;
                margin-top: 15px;

                span{
                    display: inline-block;
                    width: 100%;
                    margin: 8px 0;
                }
                
            }

            .image{
                width: 200px;
                height: 200px;
            }
        }
    }

    .sponsor-button {
        border-width: 0px;
        border-style: solid;
        padding: 5px 10px;
        margin: 15px auto;
        display: inline-block;
        cursor: pointer;
        background-color: $sponsor-button-background-color;
        -webkit-transition: transform 0.4s ease;
        -moz-transition: transform 0.4s ease;
        -o-transition: transform 0.4s ease;
        transition: transform 0.4s ease;

        &:hover {
            background-color: $sponsor-button-hover-background-color;
            @include transform(scale(1.05));
        }

        [theme="dark"] & {
            background-color: $sponsor-button-background-color-dark;

            &:hover {
                background-color: $sponsor-button-hover-background-color-dark;
            }
        }

        [theme="black"] & {
            background-color: $sponsor-button-background-color-black;

            &:hover {
                background-color: $sponsor-button-hover-background-color-black;
            }
        }

        span {
            vertical-align: top;
        }
    }
}

@media only screen and (max-width: 1000px) {
    .sponsor {
        .sponsor-bio {
            width: 50%;
        }
    }
}

@media only screen and (max-width: 680px) {
    .sponsor {
        .sponsor-bio {
            width: 75%;
        }
    }
}